import React from 'react'
import './Navbar.css'
import { Link } from 'react-router-dom'
import { useState } from 'react'
import farmer17 from '../image/farm17.png'

function Navbar() {


  const [menu, setMenu] = useState("home");


  return (
    <div className='navbar'>
      
      <div class="header">
  <div className='logo'>
  {/* <p><a>National<span> Kisan </span>Portal</a></p> */}
    <img src={farmer17}/>
  <p><a>National<span> Kisan </span>Portal</a></p>

  </div>
  <div>
  <ul>
  
  <Link to="/"> <li onClick={()=>{setMenu("home")}}><a href="#">Home{menu==="home"?<hr/>:<></>}</a></li></Link>  
 <Link to="/data"> <li onClick={()=>{setMenu("data")}}><a href="#">Crop Data{menu==="data"?<hr/>:<></>}</a></li></Link>  
 <Link to="/profile"><li onClick={()=>{setMenu("profile")}}><a>Add Crop{menu==="profile"?<hr/>:<></>}</a></li></Link> 

 {/* <Link to="/admin"> <li onClick={()=>{setMenu("admin")}}><a href="#">Admin{menu==="admin"?<hr/>:<></>}</a></li></Link>    */}
  <Link to="/mail"><li onClick={()=>{setMenu("mail")}}><a>Contact{menu==="mail"?<hr/>:<></>}</a></li></Link>
 
  
  </ul>


  </div>

   <Link to='/registartion'> <div className='login-btn'>

  <button>Login</button>
    </div></Link>

  

</div>
{/* <hr></hr> */}
    </div>
  )
}

export default Navbar
